export function readImgFileToDataURL (file) {
  return new Promise((resolve, reject) => {
    if (!file || !(file instanceof File)) {
      reject('file shoule be a File object!')
      return
    }
    let reader = new FileReader()
    reader.readAsDataURL(file)
    reader.onloadend = (ev) => {
      resolve(ev.srcElement.result)
    }
    reader.onerror = (err) => {
      reject(err)
    }
  })
}



