export default {
  props: {
    accept: {
      type: String,
      default: '*/*'
    },
    action: {
      type: String,
      required: true
    },
    formName: {
      type: String,
      default: 'file'
    },
    beforeUpload: {
      type: Function
    }
  },
  mounted () {
    this.fileInput = this.$refs.fileInput
  },
  methods: {
    fileChanged (ev) {
      if (!this.fileInput) return
      let fileInput = this.fileInput
      let file =
        fileInput.files && fileInput.files.length ? fileInput.files[0] : null
      setTimeout(() => {
        this.fileInput.value = null
      }, 50)
      if (this.beforeUpload) {
        let checkResult = this.beforeUpload(file)
        if (!checkResult) return
      }
    },
    showSelect () {
      this.fileInput && this.fileInput.click()
    }
  }
}



