import Uploder from '@/components/file-uploader'
import FormRow from '@/components/form-row'
import { readImgFileToDataURL } from '@/utils/utils'
import {
  DIRES,
  FeelTypes,
  CurlTypes,
  LenTypes,
  FaceTypes,
  HairTextures,
  HairCounts,
  EyeDistances,
  Bangs,
  ThreeParts,
  SkinColors
} from './../const'

const DialogTypesMap = {
  1: { dialog: 'showTypeDialog', valuePropPrefix: 'style' },
  2: { dialog: 'showCurlDialog', valuePropPrefix: 'curl' },
  3: { dialog: 'showLenDialog', valuePropPrefix: 'len' },
  4: { dialog: 'showFaceDialog', valuePropPrefix: 'face' },
  5: { dialog: 'showTexturesDialog', valuePropPrefix: 'hairTextures' },
  6: { dialog: 'showCountsDialog', valuePropPrefix: 'hairCounts' },
  7: { dialog: 'showEyeDistancesDialog', valuePropPrefix: 'eyeDistances' },
  8: { dialog: 'showBangsDialog', valuePropPrefix: 'bangs' },
  9: { dialog: 'showThreePartsDialog', valuePropPrefix: 'threeParts' },
  10: { dialog: 'showSkinColorsDialog', valuePropPrefix: 'skinColors' }
}

//
export default {
  components: { Uploder, FormRow },
  data () {
    let data = {
      model: {
        styleName: null,
        styleId: null,
        character: null,
        hairColor: null,
        popularElement: null,
        fitScene: null,
        fitCharacterDesc: null,
        hairStyleEffect: null,
        hairStyleTitle: null
      },
      info: {
        currentImgIndex: 0,
        actionSheetTheme: 'android',
        maxImgIndex: DIRES.length - 1,
        clientWidth: document.documentElement.clientWidth,
        inputPlaceholder: '请输入',
        inputTextAlign: 'right',
        inputShowClear: false
      },
      options: {
        types: FeelTypes,
        curlTypes: CurlTypes,
        lenTypes: LenTypes,
        faceTypes: FaceTypes,
        hairTextures: HairTextures,
        hairCounts: HairCounts,
        eyeDistances: EyeDistances,
        bangs: Bangs,
        threeParts: ThreeParts,
        skinColors: SkinColors
      }
    }
    data.model.imgs = DIRES.map((direItem, inx) => {
      return Object.assign(
        {
          imgUrl: '',
          url: '',
          isFirst: inx === 0,
          isLast: inx === DIRES.length - 1
        },
        direItem
      )
    })
    let info = data.info
    Object.values(DialogTypesMap).forEach(dialogConfig => {
      info[dialogConfig.dialog] = false
    })
    data.info = info
    return data
  },
  created () {
    this.swipeThreshold = 200
  },
  mounted () {
    this.uploader = this.$refs.uploader
    let imgsContainer = document.querySelector('.hair-type-imgs')
    let hammertime = new Hammer(imgsContainer)
    hammertime.on('swipe', ev => {
      if (ev.deltaX < 0 - this.swipeThreshold) {
        this.changeCurrentIndex(1)
      }
      if (ev.deltaX > this.swipeThreshold) {
        this.changeCurrentIndex(-1)
      }
    })
  },
  methods: {
    checkAndProcessModel (model) {
      let msgs = []
      let frontPic = model.imgs.find(img => img.value === 1)
      if (!frontPic.imgUrl) {
        msgs.push('正面图片不能为空')
      }
      if (!model.faceId) {
        msgs.push('请选择合适的脸型')
      }
      if (!model.hairStyleTitle) {
        msgs.push('请输入发型标题')
      }
      if (msgs && msgs.length) {
        this.$vux.toast.show({
          type: 'text',
          text: msgs.join('<br/>')
        })
        return false
      }
      return true
    },
    saveHairStyle () {
      let model = Object.assign({}, this.model)
      if (!this.checkAndProcessModel(model)) return
    },
    showDialog (type) {
      this.currentSelectType = type
      let dialogConfig = DialogTypesMap[type]
      this.info[dialogConfig.dialog] = true
    },
    itemClicked (inx, item) {
      let model = this.model
      if (this.currentSelectType) {
        let config = DialogTypesMap[this.currentSelectType]
        let valPrefix = config.valuePropPrefix
        model[valPrefix + 'Name'] = item.label
        model[valPrefix + 'Id'] = item.value
      }
    },
    beforeImageUpload (file) {
      file.imgItem = this.currentImgItem
      readImgFileToDataURL(file).then(result => {
        file.imgItem.imgUrl = result
      })
    },
    addImage (imgItem) {
      this.currentImgItem = imgItem
      this.uploader.showSelect()
    },
    deleteImg (imgItem) {
      this.$vux.confirm.show({
        content: '确定删除该张图片吗?',
        onConfirm () {
          imgItem.imgUrl = null
        }
      })
    },
    changeCurrentIndex (dIndex) {
      let info = this.info
      let newIndex = info.currentImgIndex + dIndex
      if (newIndex < 0) {
        newIndex = 0
      }
      if (newIndex >= info.maxImgIndex) {
        newIndex = info.maxImgIndex
      }
      info.currentImgIndex = newIndex
    }
  }
}



